import React, { useEffect } from 'react';
import { cn } from '@bem-react/classname';
import './ProfilePicture.scss';

const ProfilePicture = ({ src }) => {
  const classItem = cn('ProfilePicture');
  
  return (
    <picture className={classItem()}>
      <source srcset={src.webp} type="image/webp" width="156" height="156"></source>
      <img src={src.jpg} className={classItem('Image')} alt="Dmitriy Khanin" loading="lazy" width="156" height="156" />
    </picture>
  )
};

export default ProfilePicture;